const FooterStyled = styled.footer`
    padding: 0 9rem;
    background: #111;
    font-size: 1.4rem;
    img {
      width: 40%;
      margin-bottom: 2rem;
    }
    a {
      font-size: 1.4rem;
    }
    a:hover {
      color:#ff0000;
    }
    footer {
      padding-top: 5rem;
    }

  #footer .footer-holder {
      overflow: hidden;
      background: #111;
      color: #acacac;

      h3 {
        font-size: 18px;
        line-height: 20px;
        color: #fff;
        font-weight: 700;
        margin: 0 0 31px;
        padding: 0 0 14px;
        position: relative;
      }
      h3::before {
          position: absolute;
          content: "";
          z-index: -1;
          left: 0;
          top: auto;
          bottom: 0;
          right: auto;
          width: 60px;
          height: 2px;
          z-index: 0;
      }
      p {
        margin: 0 0 12.5px;
        font-size: 1.4rem;
        color: #fff;

      }
      .contact-list address {
          display: inline-block;
          vertical-align: top;
          font-weight: 400;
          font-style: normal;
          margin: 0;
          line-height: 1.85;
          font-size: 1.4rem;
      }
      .hour-list li a {
        font-size: 1.4rem;
      }
    } 
    .contact-list li {
      display: flex;
      flex-direction: row;
      margin: 1rem 0;

      .u-icon-u {
        font-size: 2rem;
        margin-right: 1rem;
      }

    }

    #footer .footer-area {
        padding: 10px 0 14px;
        overflow: hidden;
        background: #000;
    }

    #footer .socail-network {
      float: right;
      text-align: right;
      color: #bebebe;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      @media screen and (max-width: 550px){
        text-align: center;
        float: none;
      }

      .u-icon-u {
        font-size: 1.6rem;
      }

      
    }
    #footer .socail-network li {
        margin: 0 0 0 39px;
    }
    #footer .footer-area p {
        margin: 0;
    }
    .copyright p{
      color: #bebebe;
      font-size: 1.4rem;
    }
    .footer-copyright  {
      font-size: 1.6rem;
      a{
        font-size: 1.6rem;
      }
    }

    @media screen and (max-width: 1347px){
      padding: 5rem 14rem;
    }
    @media screen and (max-width: 1186px){
        padding: 5rem 8rem;
    }
    @media screen and (max-width: 990px){
        padding: 5rem 4rem;
    }
    @media screen and (max-width: 550px){
      .compyright-mobile {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
      }
    }

    .footer-con {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    .logo-on {
        display: flex;
        align-items: center; 
        img {
            width: 90px;
        }
    }

    .bottom-nav {
        display: flex;
        justify-content: space-between; 

    }
`;

export default Footer
